<template>
  <div class="restrictions">
    <h1>
      <strong>Restrictions in Nymble</strong>
    </h1>
    <h2 v-html="posts"></h2>
  </div>
</template>

<script>
import Vue from "vue";

import axios from "axios";
import moment from "moment";

Vue.use(require("vue-moment"));

export default {
  data() {
    return {
      posts: [],
      dateMenu: new Date()
    };
  },
  created() {
    var vm = this;

    axios
      .get("https://thskth.se/screenly/restrictions/restrictions.php")
      .then(function(response) {
        console.log(response);
        vm.posts = response.data[0].content.rendered;
      });
  }
};
</script>

<style>
h1 {
  margin: 0;
  padding: 0;
  font-size: 31px;
  font-size: 4.5vw;
  font-weight: 300;
}

h2,
h3,
h4,
h5,
h6 {
  margin: 0.3em 0;
  font-weight: 300;
}

h2 {
  font-size: 3.3vw;
}

h3 {
  color: #00b6d4;
  font-size: 2.6vw;
  font-weight: 200;
}

h4 {
  font-size: 2vw;
}

h5 {
  color: #00b6d4;
  font-size: 1.6vw;
  font-weight: 400;
}

h6 {
  font-size: 1.5vw;
  font-weight: 400;
  text-transform: uppercase;
}

blockquote {
  font-weight: 100;
  font-style: italic;
  letter-spacing: 0.1em;
  font-size: 1.7vw;
}

p {
  margin: 0.3em 0;
}

a {
  color: black;
  text-decoration: none;
}

b,
strong {
  font-weight: 500;
}

ul,
ol {
  padding-left: 30px;
}

ul {
  list-style-type: none;
}

ul li {
  padding-left: 10px;
}

ul li:before {
  content: "○";
  left: -20px;
  font-size: 3vw;
  line-height: 0;
  color: #00b6d4;
  margin-left: -10px;
  position: relative;
  vertical-align: middle;
}

ul li + li,
ol li + li {
  margin-top: 10px;
}

time {
  color: #9e9e9e;
  display: block;
  font-size: 1.6vw;
  font-weight: 300;
  letter-spacing: 0;
}

#brand-logo {
  left: 5%;
  bottom: 0;
  width: 20%;
  height: auto;
  z-index: 100;
  display: block;
  position: absolute;
}

main {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  position: absolute;
}

section {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: white;
  position: absolute;
}

.figure {
  top: 0;
  left: 0;
  right: 50%;
  bottom: 0;
  margin: 0;
  padding: 0;
  position: absolute;
  background-color: black;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}

.figure img {
  width: 100%;
  height: auto;
}

article {
  top: 0;
  left: 0;
  right: 0;
  bottom: 5%;
  overflow: hidden;
  padding: 5% 5% 0 5%;
  position: absolute;
}

.figure + article {
  left: 50%;
}

.content {
  margin-top: 10%;
  line-height: 1.5em;
}

.qrcode {
  position: fixed;
  bottom: 0;
  right: 0;
  max-width: 8%;
  height: auto;
  display: block;
  padding: 2%;
  background: white;
  z-index: 999;
}

#pagging {
  left: 55%;
  bottom: 5%;
  margin: 0;
  padding: 0;
  position: fixed;
}

#pagging li {
  margin: 0;
  padding: 0;
  width: 1.2vw;
  height: 1.2vw;
  overflow: hidden;
  list-style: none;
  border-radius: 50%;
  text-indent: -100%;
  border: 1px solid #00b6d4;
  display: inline-block;
  transition: background 0.4s;
}

#pagging li.active {
  background: #00b6d4;
}

#pagging li + li {
  margin-left: 0.8vw;
}

#pagging li:before {
  content: none;
}

body.attachment #brand-logo {
  top: 0;
  left: 5%;
  bottom: auto;
}

body.attachment main:after {
  top: 35%;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  content: "";
  display: block;
  position: absolute;

  background: -webkit-linear-gradient(
    top,
    rgba(0, 0, 0, 0) 0%,
    rgba(0, 0, 0, 1) 100%
  );
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0) 0%,
    rgba(0, 0, 0, 1) 100%
  );
}

body.attachment .figure {
  right: 0;
}

body.attachment article {
  left: 0;
  top: auto;
  bottom: 7%;
  color: white;
  z-index: 99;
}

body.attachment article h1 {
  text-indent: 0;
  margin-bottom: 10px;
}

body.attachment article .content {
  margin-top: 0;
}
.restrictions {
  padding: 0px 100px;
}
</style>



