<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>

export default {
  name: 'App'
}

</script>

<style>
  html, body
    {
        margin: 0;
        padding: 0;
        width: 100%;
        height: 100%;
        color: black;
        font-size: 16px;
        font-size: 2vw;
        font-weight: 200;
        letter-spacing: 0.04em;
    }
    
    body
    {
        width: 100%;
        height: 100%;
        background: white;
    }
    
    /*
     * There is no interaction so let's improove performance by
     * reducing interaction events
    */
    
    body:not(.debug)
    {
        user-select: none;
        pointer-events: none;
    }
    
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



