import Vue from "vue";
import Router from "vue-router";
import Restaurants from "@/components/restaurants";
import Restrictions from "@/components/restrictions";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/restrictions",
      name: "Restaurants",
      component: Restaurants
    },
    {
      name: "Restrictions",
      path: "/",
      component: Restrictions
    }
  ]
});



